import { makeStyles } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import ReactDOMServer from 'react-dom/server';
import { FormattedMessage } from 'react-intl';
import { useDispatch, useSelector } from 'react-redux';
import { Treebeard, TreeNode } from 'react-treebeard-ts';
import iconArchive from '../../assets/icons/archive.svg';
import iconLinear from '../../assets/icons/arrow-top-right.svg';
import iconCollapse from '../../assets/icons/collapse-all.svg';
import iconCopy from '../../assets/icons/copy.svg';
import iconCut from '../../assets/icons/cut.svg';
import iconCylindricalWrap from '../../assets/icons/cylindrical-wrap.svg';
import iconDelete from '../../assets/icons/delete.svg';
import iconExpand from '../../assets/icons/expand-all.svg';
import iconExternal from '../../assets/icons/external.svg';
import iconFunction from '../../assets/icons/function-variant.svg';
import iconGroup from '../../assets/icons/group.svg';
import iconImage from '../../assets/icons/image.svg';
import iconLoadImage from '../../assets/icons/load-image.svg';
import iconLoop from '../../assets/icons/loop.svg';
import iconColor from '../../assets/icons/palette.svg';
import iconPaste from '../../assets/icons/paste.svg';
import iconPerspective from '../../assets/icons/perspective.svg';
import iconPolarTransformation from '../../assets/icons/polar-transformation.svg';
import iconResize from '../../assets/icons/resize.svg';
import iconShift from '../../assets/icons/shift.svg';
import iconStatistics from '../../assets/icons/statistics.svg';
import iconTableAnalyzer from '../../assets/icons/table-analyzer.svg';
import iconTable from '../../assets/icons/table.svg';
import iconTransformation from '../../assets/icons/transformation.svg';
// import iconUndoDisabled from '../../assets/icons/undo-disabled.svg';
// import iconUndo from '../../assets/icons/undo.svg';
import iconWindow from '../../assets/icons/window.svg';
import ActionBar from '../../components/ActionBar';
import ConfirmDialog from '../../components/ConfirmDialog';
import type Configuration from '../../protobuf/Configuration';
import Protobuf from '../../protobuf/Protobuf';
import type Proxy from '../../protobuf/Proxy';
import { setEditingFocus, setParameter } from '../../redux/actions';
import logger from '../../utils/logger';
import {
  canReadToolSetView,
  canEditToolSetView,
  canAddTool,
  canEditTool,
  canDeleteTool,
} from '../../utils/permissions';
import { isMathTool, MathTool } from './MathTool';
import ToolSetBar, { ToolSetBarProps } from './components/ToolSetBar';
import ToolSetup from './components/ToolSetup';
import TreebeardStyle from './components/TreebeardStyle';

import './style.scss';

enum ActionType {
  SELECT,
  DELETE,
  ADD,
  COPY,
  CUT,
  PASTE,
}

export interface ToolsetTreeNode extends TreeNode {
  children?: ToolsetTreeNode[];
  parentNode?: ToolsetTreeNode;
  position: number;
  isValid?: boolean;
  toggled?: boolean;
  objectID?: string;
  objectType?: string | number;
  objectSubType?: string | number;
}

const sortToolSet = (a: ToolsetTreeNode, b: ToolsetTreeNode) => {
  return a.position > b.position ? 1 : -1;
};

const useStyles = makeStyles(() => ({
  label: {
    fontSize: '1.4rem',
    transform: 'translate(10px, 12px) scale(1)',
  },
}));

interface ToolsetViewProps {
  id: string;
  inspectionId: string;
}

const ToolsetView = ({ id: viewId, inspectionId }: ToolsetViewProps) => {
  const proxy = useSelector<any>((state) => state.app?.proxy) as Proxy;
  const inspections = useSelector<any>((state) => state.inspections) as any;
  const dispatch = useDispatch();
  const configuration = useSelector<any>((state) => state.app?.configuration) as Configuration;
  const inspectionToolsetMappings = useSelector<any>(
    (state) => state.app?.inspectionToolsetMappings,
  ) as Map<any, any>;
  const myPermissions = useSelector<any>((state) => state.app?.myPermissions) as Set<string>;
  const classes = useStyles();

  const [treeModel, setTreeModel] = useState<ToolsetTreeNode[]>([]);
  const [filteredTreeModel, setFilteredTreeModel] = useState<ToolsetTreeNode[] | undefined>(
    undefined,
  );
  const [cursor, setCursor] = useState<ToolsetTreeNode | null>(null);
  const lastActionsStack = useRef<
    {
      type: ActionType;
      from?: number;
      to?: number;
      position?: number;
      tool?: ToolsetTreeNode;
      toolId?: string;
    }[]
  >([]);
  const [toolSetId, setToolSetId] = useState(0);
  const [openConfirmDelete, setOpenConfirmDelete] = useState(false);
  const [filterText, setFilterText] = useState('');

  const sharedState = inspections?.[inspectionId]?.sharedState;

  useEffect(() => {
    if (configuration.inspectionToolsetMappings.size === 0) {
      configuration
        .setInspectionIdToToolsetMappings(proxy.protobuf)
        .then(() => proxy.dispatchCurrentConfiguration(configuration));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const getToolsetTreeByInspectionId = (id: string) => {
      if (!id) {
        logger.warn('getToolsetTreeByInspectionId did not receive inspection id');
        return;
      } else if (inspectionToolsetMappings.size === 0) {
        logger.warn('inspectionToolsetMappings is empty');
        return;
      }
      return inspectionToolsetMappings.get(id);
    };

    const treeDataRaw = getToolsetTreeByInspectionId(inspectionId);
    console.log(inspectionId, treeDataRaw); //!
    setToolSetId(treeDataRaw?.toolSetId);

    const createTreeNodes = (
      node: ToolsetTreeNode | undefined | null,
      parentNode?: ToolsetTreeNode,
    ) => {
      if (!node) {
        return null;
      }

      const { name, isValid, position, objectID, objectType, objectSubType } = node;

      const isLeaf = (child: ToolsetTreeNode) =>
        !child.children?.length && child.position === -1 && !child.objectType;

      const isMathToolNodeOrLeaf = isMathTool(node) || (isMathTool(parentNode) && isLeaf(node));

      const toolsetTreeNode: ToolsetTreeNode = {
        parentNode,
        name: isMathToolNodeOrLeaf
          ? name
          : ((<span style={{ color: '#546E7A' }}>{name}</span>) as never),
        isValid,
        position,
        objectID,
        toggled: false,
        objectType,
        objectSubType,
      };

      toolsetTreeNode.children = node.children?.reduce((acc, node) => {
        const treeNode = createTreeNodes(node, toolsetTreeNode);
        if (treeNode) {
          acc.push(treeNode);
        }
        return acc;
      }, [] as ToolsetTreeNode[]);

      return toolsetTreeNode;
    };

    const tree = createTreeNodes(treeDataRaw);

    if (!tree?.children?.length) {
      return;
    }

    const treeModel = tree.children.sort(sortToolSet);
    setTreeModel(treeModel);
  }, [inspectionToolsetMappings, inspectionId]);

  useEffect(() => {
    if (sharedState.editingViewId !== viewId) {
      updateTreeModel(
        () => true,
        () => ({
          active: false,
        }),
      );
      setCursor(null);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sharedState.editingViewId]);

  const openMathToolTab = useCallback(
    (tabNode: ToolsetTreeNode) => {
      if (!sharedState || !tabNode.parentNode) {
        return;
      }
      if (sharedState.editingViewId !== viewId) {
        dispatch(setEditingFocus(inspectionId, viewId));
      }

      dispatch(
        setParameter({
          component: (
            <div className="math-tool-parameter-view">
              <MathTool
                inspectionID={parseInt(inspectionId)}
                data={tabNode.parentNode}
                view={tabNode.name}
              />
            </div>
          ),
        }),
      );
    },
    [dispatch, inspectionId, sharedState, viewId],
  );

  const updateTreeModel = useCallback(
    (
      targetNode: ToolsetTreeNode | ((node: ToolsetTreeNode) => boolean),
      updateTargetData: (node: ToolsetTreeNode) => Partial<ToolsetTreeNode>,
      updateOtherData?: (node: ToolsetTreeNode) => Partial<ToolsetTreeNode>,
    ) => {
      const mapToolsetTree = (tree: ToolsetTreeNode[]): ToolsetTreeNode[] => {
        return tree.map((node) => {
          const children = node.children?.length ? mapToolsetTree(node.children) : node.children;
          if (typeof targetNode === 'function' ? targetNode(node) : node === targetNode) {
            return { ...node, children, ...updateTargetData(node) };
          } else {
            return updateOtherData ? { ...node, children, ...updateOtherData(node) } : node;
          }
        });
      };
      setTreeModel(mapToolsetTree);
    },
    [],
  );

  const onNodeClicked = useCallback(
    (node: ToolsetTreeNode, toggled: boolean) => {
      const isLeaf = !node.children?.length && node.position === -1 && !node.objectType;

      updateTreeModel(
        node,
        (node) => ({
          active: isLeaf && toggled,
          toggled: node.children?.length ? toggled : node.toggled,
        }),
        () => ({ active: false }),
      );

      lastActionsStack.current.push({
        type: ActionType.SELECT,
        from: cursor?.position,
        to: node.position,
      });

      setCursor(isLeaf && toggled ? node : null);

      if (isLeaf && isMathTool(node.parentNode)) {
        openMathToolTab(node);
      }
    },
    [cursor?.position, openMathToolTab, updateTreeModel],
  );

  const setNext = useCallback(() => {
    if (!cursor) {
      return;
    }
    const { position } = cursor;
    updateTreeModel(
      (node) => node.position === position + 1,
      () => ({ active: true }),
      () => ({ active: false }),
    );
  }, [cursor, updateTreeModel]);

  const deleteTool = useCallback(
    (objectId: string) => {
      if (!cursor) {
        return;
      }
      const { position } = cursor;
      lastActionsStack.current.push({
        type: ActionType.DELETE,
        tool: { ...cursor, active: false },
      });

      proxy.deleteTool(inspectionId, objectId);
      setTreeModel(treeModel.filter((treeNode) => treeNode.position !== position));
      setNext();
    },
    [cursor, inspectionId, proxy, setNext, treeModel],
  );

  const handleConfirmDeleteClose = (confirmed: boolean) => {
    if (confirmed && cursor?.objectID) {
      deleteTool(cursor.objectID);
    }
    setOpenConfirmDelete(false);
  };

  const viewActions = useMemo(() => {
    // const setPrevious = () => {
    //   if (!cursor) {
    //     return;
    //   }
    //   const { position } = cursor;
    //   updateTreeModel(
    //     (node) => node.position === position - 1,
    //     () => ({ active: true }),
    //     () => ({ active: false }),
    //   );
    // };

    // const selectPrevious = () => {
    //   if (!cursor) {
    //     return;
    //   }
    //   const { position } = cursor;
    //   setPrevious();

    //   lastActionsStack.current.push({
    //     type: ActionType.SELECT,
    //     from: position,
    //     to: position - 1,
    //   });
    // };

    // const selectNext = () => {
    //   if (!cursor) {
    //     return;
    //   }
    //   const { position } = cursor;
    //   setNext();

    //   lastActionsStack.current.push({
    //     type: ActionType.SELECT,
    //     from: position,
    //     to: position + 1,
    //   });
    // };

    // const undoLastAction = () => {
    //   if (lastActionsStack.current.length === 0) {
    //     return;
    //   }

    //   const lastAction = lastActionsStack.current.pop();

    //   switch (lastAction?.type) {
    //     case ActionType.SELECT:
    //       setTreeModel((tree) =>
    //         tree.map((treeNode) => {
    //           if (treeNode.position === lastAction.from) {
    //             if (cursor) {
    //               cursor.active = false;
    //             }
    //             treeNode.active = true;
    //             setCursor(treeNode);
    //           }
    //           return treeNode;
    //         }),
    //       );
    //       break;
    //     case ActionType.DELETE:
    //       // treeModel.push(lastAction.tool);
    //       // setTreeModel(treeModel.sort(sortToolSet));
    //       // setPrevious();
    //       break;
    //     case ActionType.ADD:
    //       lastAction.toolId && deleteTool(lastAction.toolId);
    //       break;
    //     default:
    //   }
    // };

    const expandTools = () => {
      updateTreeModel(
        () => true,
        (node) => ({ toggled: !!node.children?.length }),
      );
    };

    const collapseTools = () => {
      updateTreeModel(
        () => true,
        () => ({ toggled: false }),
      );
    };

    const copyTool = async () => {
      if (!cursor) {
        return;
      }
      const { objectID } = cursor;
      lastActionsStack.current.push({
        type: ActionType.COPY,
        tool: { ...cursor, active: false },
      });

      await proxy.copyTool(inspectionId, objectID);
    };

    const cutTool = async () => {
      if (!cursor) {
        return;
      }
      const { position, objectID } = cursor;
      lastActionsStack.current.push({
        type: ActionType.CUT,
        tool: { ...cursor, active: false },
      });

      await proxy.cutTool(inspectionId, objectID);
      setTreeModel(treeModel.filter((treeNode) => treeNode.position !== position));
      setNext();
    };

    const pasteTool = async () => {
      if (!cursor) {
        return;
      }
      const { objectID } = cursor;
      lastActionsStack.current.push({
        type: ActionType.PASTE,
        tool: { ...cursor, active: false },
      });

      await proxy.pasteTool(inspectionId, objectID);
    };

    const actions = [
      { intlId: 'copy', iconUrl: iconCopy, onClickHandler: copyTool },
      { intlId: 'cut', iconUrl: iconCut, onClickHandler: cutTool },
      { intlId: 'paste', iconUrl: iconPaste, onClickHandler: pasteTool },
      // {
      //   intlId: 'undo',
      //   iconUrl: lastActionsStack.current.length === 0 ? iconUndoDisabled : iconUndo,
      //   onClickHandler: () => (lastActionsStack.current.length === 0 ? void 0 : undoLastAction()),
      // },
      { intlId: 'expandAll', iconUrl: iconExpand, onClickHandler: expandTools },
      { intlId: 'collapseAll', iconUrl: iconCollapse, onClickHandler: collapseTools },
    ];

    if (canDeleteTool(myPermissions)) {
      actions.push({
        intlId: 'delete',
        iconUrl: iconDelete,
        onClickHandler: () => setOpenConfirmDelete(true),
      });
    }

    return actions;
  }, [cursor, inspectionId, proxy, setNext, treeModel, updateTreeModel, myPermissions]);

  const toolActions = useMemo<ToolSetBarProps['tools']>(() => {
    const addTool = async (type: ActionType) => {
      const position = cursor?.position ?? 0;
      const newToolId = await proxy.addTool(inspectionId, toolSetId, type, position);
      lastActionsStack.current.push({
        type: ActionType.ADD,
        position,
        toolId: newToolId,
      });
    };
    return [
      {
        intlId: 'toolsetView.windowTool',
        iconUrl: iconWindow,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.WindowToolClassId),
      },
      {
        intlId: 'toolsetView.mathTool',
        iconUrl: iconFunction,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.MathToolClassId),
      },
      {
        intlId: 'toolsetView.statisticsTool',
        iconUrl: iconStatistics,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.StatisticsToolClassId),
      },
      {
        intlId: 'toolsetView.ringBufferTool',
        iconUrl: iconLinear,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.RingBufferToolClassId),
      },
      {
        intlId: 'toolsetView.archiveTool',
        iconUrl: iconArchive,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.ArchiveToolClassId),
      },
      {
        intlId: 'toolsetView.acquisitionTool',
        iconUrl: iconLinear,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.AcquisitionToolClassId),
      },
      {
        intlId: 'toolsetView.transformationTool',
        iconUrl: iconTransformation,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.TransformationToolClassId),
      },
      {
        intlId: 'toolsetView.shiftTool',
        iconUrl: iconShift,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.ShiftToolClassId),
      },
      {
        intlId: 'toolsetView.cylindricalWarpTool',
        iconUrl: iconCylindricalWrap,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.CylindricalWarpToolClassId),
      },
      {
        intlId: 'toolsetView.perspectiveTool',
        iconUrl: iconPerspective,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.PerspectiveToolClassId),
      },
      {
        intlId: 'toolsetView.imageTool',
        iconUrl: iconImage,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.ImageToolClassId),
      },
      {
        intlId: 'toolsetView.linearTool',
        iconUrl: iconLinear,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.LinearToolClassId),
      },
      {
        intlId: 'toolsetView.colorTool',
        iconUrl: iconColor,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.ColorToolClassId),
      },
      {
        intlId: 'toolsetView.resizeTool',
        iconUrl: iconResize,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.ResizeToolId),
      },
      {
        intlId: 'toolsetView.loadImageTool',
        iconUrl: iconLoadImage,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.LoadImageToolClassId),
      },
      {
        intlId: 'toolsetView.tableTool',
        iconUrl: iconTable,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.TableToolClassId),
      },
      {
        intlId: 'toolsetView.tableAnalyzerTool',
        iconUrl: iconTableAnalyzer,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.TableAnalyzerToolClassId),
      },
      {
        intlId: 'toolsetView.polarTransformationTool',
        iconUrl: iconPolarTransformation,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.PolarTransformationToolClassId),
      },
      {
        intlId: 'toolsetView.loopTool',
        iconUrl: iconLoop,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.LoopToolClassId),
      },
      {
        intlId: 'toolsetView.externalTool',
        iconUrl: iconExternal,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.ExternalToolClassId),
      },
      {
        intlId: 'toolsetView.groupTool',
        iconUrl: iconGroup,
        onClickHandler: () => addTool(Protobuf.SvPb.ClassIdEnum.GroupToolClassId),
      },
    ];
  }, [cursor, inspectionId, proxy, toolSetId]);

  const onChangeFilter: React.ChangeEventHandler<HTMLTextAreaElement | HTMLInputElement> = (
    event,
  ) => {
    const text = event.target.value;
    setFilterText(text);

    const includesTextInReactElement = (element: ReactElement) => {
      try {
        return (
          ReactDOMServer.renderToString(element).toLowerCase().indexOf(text.toLowerCase()) > -1
        );
      } catch (e) {
        return false;
      }
    };
    setFilteredTreeModel(
      text
        ? treeModel.filter((item) =>
            typeof item.name === 'string'
              ? item.name.toLowerCase().indexOf(text.toLowerCase()) > -1
              : includesTextInReactElement(item.name),
          )
        : undefined,
    );
  };

  if (canReadToolSetView(myPermissions) || canEditToolSetView(myPermissions)) {
    return (
      <div className="toolset-wrapper">
        <ConfirmDialog
          open={openConfirmDelete}
          onClose={(confirmed: boolean) => handleConfirmDeleteClose(confirmed)}
          message={
            <FormattedMessage id="toolsetView.confirmDelete" values={{ toolName: cursor?.name }} />
          }
        />
        {canAddTool(myPermissions) && <ToolSetBar tools={toolActions} />}
        <div className="toolset">
          <div className="overflow-auto h-100 w-100" style={{ paddingBottom: 24 }}>
            <div>
              {cursor && <ToolSetup node={cursor} />}
              <div className="w100">
                <TextField
                  label="Filter"
                  type="text"
                  variant="outlined"
                  style={{ width: '100%' }}
                  onChange={onChangeFilter}
                  value={filterText}
                  InputLabelProps={{ classes: { root: classes.label } }}
                />
              </div>
              <Treebeard
                data={filteredTreeModel ?? treeModel}
                onToggle={(node, toggled) => onNodeClicked(node as never, toggled)}
                style={TreebeardStyle}
              />
            </div>
          </div>
        </div>
        {canEditTool(myPermissions) && <ActionBar isViewAction actions={viewActions} />}
      </div>
    );
  }
  return <div></div>;
};

export default ToolsetView;
